				"html": "<p>The weather in Southern California has improved greatly from the first few weeks of 2017. It's almost felt Spring like! The result is more—more surfing, more riding bikes and even still a little time to skateboard and rock climb. Besides enjoying the year's first sunny days, I released <a href=\"https://dollarshaveclub.github.io/scrolldir/\">ScrollDir</a> and continues to research into what I'm calling <strong>story projects</strong>. <strong>Story projects</strong>, 3 are started, are basically versions of serverside rendering capable boilerplate blog sites with different frontend MVCs.</p>\n<h2>ScrollDir, scroll direction detection</h2>\n<figure>\n  <img src=\"//yowainwright.imgix.net/wk-3/scrolldir.png?w=800&h=400&crop=focalpoint&auto=format\" alt=\"Vertical scroll direction without jitter\" />\n</figure>\n<p>A few months ago, I had a projects which required me to perfectly maintain scroll direction for showing and hiding user interface elements based on scroll direction. In writing something basic to do this, my co-worker <a href=\"https://github.com/pwfisher\">Patrick Fisher</a> mentioned that he'd written a <a href=\"http://jquery.com/\">jQuery</a> plugin for this a few years ago. I implemented plugin into my project and it worked perfectly so we modernized it a bit for current JavaScript standards. The end result was ScrollDir. ScrollDir kills the need for developers needing to worry about a tool for maintaining vertical scroll direction. It's lightweight and easy to include in projects.</p>\n<h2>Roads seem clearest</h2>\n<blockquote>\n<p>Roads seem clearest when illuminated by light piercing through darkness</p>\n</blockquote>\n<p>Over the last couple of weeks I've been really concerned about the state of the world. Not going into that here, I'll say I've been looking to the outdoors for hope. The photo below was taken up off of Angeles Crest highway. </p>\n<figure>\n  <img src=\"//yowainwright.imgix.net/wk-3/angeles-highway.jpg?w=2000&h=1500&crop=focalpoint&auto=format\" alt=\"Angeles Highway\" />\n</figure>",
